<template>
  <div class='dashboard'>
    <el-row>
      <el-col :span="24"><h4></h4></el-col>
    </el-row>
    <el-row>
      <el-col :span="12"><div style="line-height: 40px; font-size: 14px;">1000&nbsp;&nbsp;&nbsp;&nbsp;900&nbsp;&nbsp;&nbsp;&nbsp;<span style="color: red">50</span>&nbsp;&nbsp;&nbsp;&nbsp;50</div></el-col>
      <el-col :span="12" style="line-height: 40px; text-align: right">
          : <div class="markPic" style="background: #F56C6C"></div>
          : <div class="markPic" style="background: #409EFF"></div>
          : <div class="markPic" style="background: #909399"></div>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <div class="grid-content bg-purple-dark" style="height:900px; border: 1px solid #666" id="allmap"></div>
      </el-col>
    </el-row>
  </div>
</template>
<script>
  export default {
    name: 'dashboard',
    data () {
        return {

        }
    },
    mounted() {
      this.fetchData();
      this.mapview();
    },
    methods: {
      fetchData() {
        //projectlist()
      },
      mapview() {
        // API
        let map = new BMap.Map("allmap") // Map
        map.centerAndZoom(new BMap.Point(117.233443, 31.826578), 13) // ,
        map.addControl(new BMap.NavigationControl()) //
        map.enableScrollWheelZoom(true) //
        // 
        var infoWindow = new BMap.InfoWindow(
            `<div>
                <h3 style="font-size: 12px"></h3>
                <p style="font-size: 12px; color: #F56C6C;">(001)</p>
                <p style="font-size: 12px; color: #409EFF">(002)</p>
                <p style="font-size: 12px; color: #909399">(003)</p>
            </div>`, {
            width : 250,
            height: 250,
            offset: {
                width: 0,
                height: -15
            },
            title : `<div style="font-size: 14px; color: rgb(77, 77, 77); font-weight: bold; text-decoration: none;">XXXX</div>`, 
        })
        // 1
        let pt1 = new BMap.Point(117.235443, 31.826578)
	    let myIcon1 = new BMap.Icon("static/img/icon1.png", new BMap.Size(25, 45))
        let marker1 = new BMap.Marker(pt1, {icon: myIcon1});  // 
        marker1.addEventListener('click', (e) => marker1.openInfoWindow(infoWindow))
        map.addOverlay(marker1)
        // 2
        let pt2 = new BMap.Point(117.295943, 31.857578)
	    let myIcon2 = new BMap.Icon("static/img/icon2.png", new BMap.Size(25, 45))
	    let marker2 = new BMap.Marker(pt2, {icon: myIcon2});  // 
        marker2.addEventListener('click', (e) => marker2.openInfoWindow(infoWindow))
        map.addOverlay(marker2)
        // 3
        let pt3 = new BMap.Point(117.135443, 31.806578)
	    let myIcon3 = new BMap.Icon("static/img/icon3.png", new BMap.Size(25, 45))
	    let marker3 = new BMap.Marker(pt3, {icon: myIcon3});  // 
        marker3.addEventListener('click', (e) => marker3.openInfoWindow(infoWindow))
        map.addOverlay(marker3)
        // 4
        let pt4 = new BMap.Point(117.335443, 31.796578)
	    let myIcon4 = new BMap.Icon("static/img/icon1.png", new BMap.Size(25, 45))
	    let marker4 = new BMap.Marker(pt4, {icon: myIcon4});  // 
        marker4.addEventListener('click', (e) => marker4.openInfoWindow(infoWindow))
        map.addOverlay(marker4)
      }
    }
  }

</script>

<style scoped>
    .markPic {
      width: 50px;
      height: 30px;
      display: inline-block;
      vertical-align: middle;
      margin-left: 10px;
    }
</style>



