<template>
 <div class='check'>
    <el-form :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item label="">
            <el-input v-model="formInline.name"></el-input>
        </el-form-item>
        <el-form-item label="">
            <el-date-picker
            format="yyyy-MM-dd" value-format="yyyy-MM-dd"
            v-model="formInline.time"
            type="daterange"
            range-separator=""
            start-placeholder=""
            end-placeholder="">
            </el-date-picker>
        </el-form-item>       
        <el-form-item label="">
            <el-select v-model="formInline.state">               
                <el-option label="" value=""></el-option>
                <el-option label="" value="0"></el-option>
                <el-option label="" value="1"></el-option>
                <el-option label="" value="2"></el-option>              
            </el-select>      
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="query()"></el-button>
            <el-button type="primary" @click="reset()" plain></el-button>
        </el-form-item>
    </el-form>
    <el-table
    :data="tableData"
    v-loading="loading" 
    style="width: 100%">
        <el-table-column
        prop="applyTime"
        label=""
        >
        </el-table-column>
        <el-table-column
        prop="name"
        label=""
        >
        </el-table-column>
        <el-table-column
        prop="linkmanPhone"
        label="">
        </el-table-column>
         <el-table-column
        prop="areaPathName"
        label="">
        </el-table-column>
         <el-table-column
        prop="location"
        label=""
         width="80"
        >
        <template slot-scope="scope">
            <el-button type="text" size="small" @click="open(scope.row.licensePic)"></el-button>
        </template>
        </el-table-column>
        <el-table-column
        label="" 
         width="100"   
        >
        <template slot-scope="scope">
        <el-tag
            :type="state(scope.row.state).type"
            close-transition>{{state(scope.row.state).value}}</el-tag>
        </template>
        </el-table-column>
        <el-table-column
        prop="examineTime"
        label=""
         width="180"
        >
        </el-table-column>
        <el-table-column
        label=""
        width="100">
        <template slot-scope="scope">
             <el-button type="text" size="small"  v-show="scope.row.state === 0 ||scope.row.state === null ? true:false" @click='check(1,scope.row.id)'></el-button>
            <el-button type="text" size="small"  v-show="scope.row.state === 0 ||scope.row.state === null? true:false"  @click='check(2,scope.row.id)'></el-button>
        </template>
        </el-table-column>
    </el-table>
      <el-pagination
      background
      v-show="pageShow"
      style="text-align:center;" class="mt20"
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      :page-size="currentPageSize"
      layout="prev, pager, next, jumper"
      :total="total">
    </el-pagination>
 </div>
</template>
<script>
  import {customerCheckPageList,customerAgree,customerRefuse} from '../../config/api'
   export default{
        data(){
            return{
                project:[],
                formInline: {
                name:'',
                time:'',
                state:''
                },  
                loading:true,
                tableData:[],
                msg:'',
                // 
                pageShow:false,
                total: 0,
                currentPage: 1,
                currentPageSize: 10,
            }
        },
        created () {     
          this.customerCheckPageList();
        },
        methods:{
               //
                query(){
                this.loading=true;
                this.currentPage = 1;
                this.customerCheckPageList()
                },
                reset() {
                    // 
                    this.formInline = {
                    name:'',
                    time:'',
                    state:''
                    }
                    this.customerCheckPageList()
                },
                customerCheckPageList(){
                    customerCheckPageList({
                        name :this.formInline.name,
                        applyStartTime :this.formInline.time==null?'':this.formInline.time[0],
                        applyEndTime :this.formInline.time==null?'':this.formInline.time[1],
                        state:this.formInline.state, 
                        pageSize:this.currentPageSize,
                        pageIndex:this.currentPage 
                    }).then((resp) => {
                        console.log(resp)
                        this.msg=resp.msg
                        this.loading=false;
                        if(resp.code=="1"){
                        this.tableData=resp.data.list
                        this.total = parseInt(resp.data.total);
                            if(this.total>10){
                                this.pageShow=true
                            }else{
                                this.pageShow=false
                            }
                        }else{
                            this.$alert(resp.msg, '', {type: 'error'}).then(() => {}).catch(() => {});
                        }
                    }); 
                },
                state(status){
                    switch (status) {
                            case 0:
                            return {value: '', type: 'warning'}
                            case 1:
                            return {value: '', type: 'success'}
                            case 2:
                            return {value: '', type: 'danger'}
                            default:
                            return {value: '', type: ''}     
                        }
                },

                 //
                handleCurrentChange(val) {
                    this.loading=true      
                    this.currentPage = val;
                    this.customerCheckPageList();
                },
                 //
                check(state,id){
                    if(state==1){
                    this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                    }).then(() => {
                    customerAgree({id:id}).then((resp)=>{
                    if(resp.code==1){
                        this.$message({
                        type: 'success',
                        message: '!',
                        duration:1000
                    });
                    this.customerCheckPageList();
                    }else{
                        this.$alert('', '', {type: 'error'}).then(() => {}).catch(() => {});
                    }
                        })
                    
                    }).catch(() => {});
                    }else{
                    this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                    }).then(() => {
                    customerRefuse({id:id}).then((resp)=>{
                    if(resp.code==1){
                        this.$message({
                        type: 'success',
                        message: '!',
                        duration:1000
                    });
                    this.customerCheckPageList();
                    }else{
                        this.$alert('', '', {type: 'error'}).then(() => {}).catch(() => {});
                    }
                        })
                    
                    }).catch(() => {});
                    }
                

                },
                open(url){
                     this.$alert(
                         `<div>
                         <img src="${this.msg+url}">
                         </div>`,
                         '', {
                        dangerouslyUseHTMLString: true,
                        showConfirmButton:false
                        }).then(() => {}).catch(() => {});
                },

        }

   }
</script>


