<template>
  <div class="changeProject">
    <el-form ref="projectForm"
      :model="projectForm"
      :rules="rules"
      label-width="130px"
      v-loading="loading">
      <el-row>
        <el-col :span="12">
          <el-form-item label="" prop="name">
            <el-input
              v-model.trim="projectForm.name"
              maxLength="50"
              placeholder="50">
            </el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="()">
            <el-input
              v-model.trim="projectForm.code"
              maxLength="20"
              placeholder="20">
            </el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="" prop="time">
            <el-date-picker
              v-model="projectForm.time"
              type="daterange"
              format="yyyy-MM-dd"
              value-format="yyyy-MM-dd"
              range-separator=""
              start-placeholder=""
              end-placeholder="">
            </el-date-picker>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="" prop="unit">
            <el-input
              v-model.trim="projectForm.unit"
              maxLength="20"
              placeholder="20">
            </el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="" prop="user">
            <el-input
              v-model.trim="projectForm.user"
              placeholder="">
            </el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="" prop="mobile">
            <el-input
              v-model.trim="projectForm.mobile"
              placeholder=""
            ></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="" prop="area">
            <el-col :span="8">
              <el-form-item prop="province">
                <el-select
                  v-model="projectForm.province"
                  placeholder=""
                  :change="getProvince()">
                  <el-option
                    v-for="item in provinceList"
                    :key="item.id"
                    :label="item.name"
                    :value="item.name">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item prop="city">
                <el-select
                  v-model="projectForm.city"
                  placeholder=""
                  :change="getCity()">
                  <el-option
                    v-for="item in cityList"
                    :key="item.id"
                    :label="item.name"
                    :value="item.name">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item prop="county">
                <el-select
                  v-model="projectForm.county"
                  placeholder=""
                  :change="getCounty()">
                  <el-option
                    v-for="item in countyList"
                    :key="item.id"
                    :label="item.name"
                    :value="item.name">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="">
            <el-input
              v-model.trim="projectForm.abbreviation"
              maxLength="10"
              placeholder="10">
            </el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="24">
          <el-form-item label="" prop="map">
            <!--  -->
            <div v-loading="mapLoding" style="height: 400px" id="projectMap"></div>
            <!--  -->
            <el-row style="position: absolute; top: 5px; right: 5px; width: 300px;">
              <el-col :span="18">
                <el-input
                  placeholder=""
                  v-model.trim="search"
                  prefix-icon="el-icon-location"
                  size="mini"
                >
                </el-input>
              </el-col>
              <el-col :span="5" :offset="1">
                <el-button
                  size="mini"
                  type="primary"
                  @click="searchAddress()"
                ></el-button>
              </el-col>
            </el-row>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="24">
          <el-form-item label="" prop="address">
            <el-input
              placeholder=""
              v-model.trim="projectForm.address"
              :disabled="true"
            >
            </el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row style="text-align:center">
        <el-col :span="24">
          <el-form-item>
            <el-button
              type="primary"
              @click="submitForm('projectForm')"
              :disabled='disabled'></el-button>
            <el-button type="primary" @click="closeForm()" plain></el-button>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
  </div>
</template>

<script>
import {
  projectGetById,
  projectAdd,
  projectUpdate,
  areaGetPaths,
  areaGetSons
} from '../../config/api'
export default {
  name: 'changeProject',
  data () {
    var checkMobile = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(''))
      }
      setTimeout(() => {
        if (!/[-\d]+/.test(value)) {
          callback(new Error(''))
        }
        callback()
      }, 1000)
    }
    return {
      projectForm: {
        name: '', // 
        code: '', // 
        time: [], // 
        unit: '', // 
        user: '', //
        mobile: '', // 
        abbreviation: '', // 
        address: '', // 
        province: '', // 
        city: '', // 
        county: '', // /
        provinceId: 0, // ID
        cityId: 0, // ID
        countyId: 0, // /ID
        latitude: 39.915119, // 
        longitude: 116.403963 // 
      },
      search: '', // 
      map: null, // 
      provinceList: [], // 
      cityList: [], // 
      countyList: [], // 
      provinceWatch: 0, // Id
      cityWatch: 0, // Id
      countyWatch: 0, // Id
      loading: true, // loading
      disabled: true, // 
      mapLoding: true, // 
      page: Number(this.$route.query.page) || 1, // 
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 50, message: '50', trigger: 'blur' }
        ],
        time: [{ required: true, message: '', trigger: 'change' }],
        unit: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 20, message: '20', trigger: 'blur' }
        ],
        user: [{ required: true, message: '', trigger: 'blur' }],
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validate: checkMobile, trigger: 'blur' }
        ],
        area: [{ required: true, message: ' ', trigger: 'blur' }],
        province: [{ required: true, message: '', trigger: 'change' }],
        city: [{ required: true, message: '', trigger: 'change' }],
        county: [{ required: true, message: '/', trigger: 'change' }],
        map: [{ required: true, message: ' ', trigger: 'blur' }],
        address: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  methods: {
    submitForm (formName) {
      // 
      this.$refs[formName].validate((valid) => {
        if (!this.projectForm.name
          || this.projectForm.time.length == 0
          || !this.projectForm.unit
          || !this.projectForm.user
          || !this.projectForm.mobile
          || !this.projectForm.address
          || !this.projectForm.province
          || !this.projectForm.city
          || !this.projectForm.county)
          return console.log('')
        let formData = {
          address: this.projectForm.address || '',
          areaId: this.projectForm.countyId || 0,
          chargeMen: this.projectForm.user || '',
          code: this.projectForm.code || '',
          startTime: this.projectForm.time[0] || '',
          endTime: this.projectForm.time[1] || '',
          id: this.$route.params.id || 1,
          latitude: this.projectForm.latitude || 0,
          longitude: this.projectForm.longitude || 0,
          ownerName: this.projectForm.unit || '',
          phone: this.projectForm.mobile || '',
          projectName: this.projectForm.name || '',
          projectShortName: this.projectForm.abbreviation || ''
        }
        this.loading = true
        this.disabled = true
        console.log(this.$route.params.id, 'this.$route.params.id')
        Number(this.$route.params.id) != 0 ? 
          projectUpdate(formData).then((res) => {
            if (Number(res.code) == 1) {
              // 
              this.$message({
                message: '',
                type: 'success',
                duration: 2000
              })
              setTimeout(() => {
                this.$router.push({path: '/project', query: {page: this.page}})
                this.loading = false
                this.disabled = false
              } ,2000)
            } else {
              this.$alert(res.msg, '', {type: 'error'}).then(() => {}).catch(() => {})
              this.loading = false
              this.disabled = false
            }
          }).catch((err) => {
            this.loading = false
            this.disabled = false
          }) 
        : 
          projectAdd(formData).then((res) => {
            if (Number(res.code) == 1) {
              // 
              this.$message({
                message: '',
                type: 'success',
                duration: 2000
              })
              setTimeout(() => {
                this.page = 1
                this.$router.push({path: '/project', query: {page: 1}})
                this.loading = false
                this.disabled = false
              } ,2000)
            } else {
              this.$alert(res.msg, '', {type: 'error'}).then(() => {}).catch(() => {})
              this.loading = false
              this.disabled = false
            }
          }).catch((err) => {
            this.loading = false
            this.disabled = false
          })
      })
    },
    closeForm () {
      // 
      this.$router.push({path: '/project', query: {page: this.page}})
      return
      // 
      if (this.$route.params.id) {
        return
        this.getProjectData(this.$route.params.id)
      } else {
        this.$refs['projectForm'].resetFields()
        this.projectForm.code = ''
        this.projectForm.abbreviation = ''
        this.search = ''
        this.cityList = []
        this.countyList = []
      }
    },
    mapview (lng, lat) {
      // API
      this.map = new BMap.Map("projectMap") // Map
      let newPoint = new BMap.Point(Number(lng).toFixed(6), Number(lat).toFixed(6))
      this.map.centerAndZoom(newPoint, 14) // ,
      let marker = new BMap.Marker(newPoint) // 
			this.map.addOverlay(marker) // 
			this.map.panTo(newPoint)
      this.map.enableScrollWheelZoom(true) //
      var top_left_navigation = new BMap.NavigationControl() //
      //
      this.map.addControl(top_left_navigation)
      // 
      var geocoder= new BMap.Geocoder()
      this.map.addEventListener("click", (e) => { //
        geocoder.getLocation(e.point, (rs) => {
          this.projectForm.address = rs.address // 
          this.projectForm.latitude = rs.point.lat // 
          this.projectForm.longitude = rs.point.lng // 
        })
      })
      this.mapLoding = false
    },
    searchAddress () {
      this.mapLoding = true
      // 
      let local = null
      local = new BMap.LocalSearch(this.map, { //
        onSearchComplete: res => this.searchFun(res)
      })
      local.search(this.search)
    },
    searchFun (getResults) {
      let marker, point = null
      //
      if(getResults != undefined) {
        this.map.clearOverlays() //
        if(getResults.getPoi(0)) {
            point =getResults.getPoi(0).point //
            this.map.centerAndZoom(point, 18)
            marker = new BMap.Marker(point) // 
            this.map.addOverlay(marker) // 
            marker.enableDragging() // 
            console.log(`: ${point.lng}: ${point.lat}`)
        } else {
          this.$message({
            message: '',
            type: 'error',
            duration: 2000
          })
        }
      } else {
        this.$message({
          message: '',
          type: 'error',
          duration: 2000
        })
      }
      this.mapLoding = false
    },
    getSons (id, type, flag) {
      // 
      areaGetSons({id: Number(id)}).then((res) => {
        if (Number(res.code) != 1)
          return this.$alert(res.msg, '', {type: 'error'}).then(() => {}).catch(() => {})
        switch (type) {
          case 1:
            this.provinceList = res.data
            if (flag) {
              this.projectForm.province = res.data[0].name
              this.projectForm.provinceId = res.data[0].id
            }
            break
          case 2:
            if (res.data[0].id != 0) {
              this.cityList = res.data
              if (flag) {
                this.projectForm.city = res.data[0].name
                this.projectForm.cityId = res.data[0].id
              }
            }
            break
          case 3:
            if (res.data[0].id != 0) {
              this.countyList = res.data
              if (flag) {
                this.projectForm.county = res.data[0].name
                this.projectForm.countyId = res.data[0].id
              }
            }
            break
          default:
            this.provinceList = res.data
            if (flag) {
              this.projectForm.province = res.data[0].name
              this.projectForm.provinceId = res.data[0].id
            }
            break
        }
      }).catch((err) => {
      })
    },
    getProjectData (id) {
      // 
      this.loading = true
      this.disabled = true
      projectGetById({id: Number(id)}).then((res) => {
        if (Number(res.code) == 1) {
          // 
          let areaData = res.data.areaPath.split(',') || {}
          Object.assign(this.projectForm, {
            name: res.data.projectName || '', // 
            code: res.data.code || '', // 
            time: [res.data.startTime, res.data.endTime] || [], // 
            unit: res.data.ownerName || '', // 
            user: res.data.chargeMen || '', //
            mobile: res.data.phone || '', // 
            abbreviation: res.data.projectShortName || '', // 
            address: res.data.address || '', // 
            provinceId: areaData[1] || 0, // 
            cityId: areaData[2] || 0, // 
            countyId: areaData[3] || 0, // ()
            latitude: res.data.latitude || 0, // 
            longitude: res.data.longitude || 0 // 
          })
          this.mapview(res.data.longitude, res.data.latitude)
          areaGetPaths({id: Number(areaData[3]) || 0}).then((resp) => {
            if (Number(resp.code) != 1) {
              this.$alert(res.msg, '', {type: 'error'}).then(() => {}).catch(() => {})
            } else {
              Object.assign(this.projectForm, {
                province: resp.data[0].name,
                city: resp.data[1].name,
                county: resp.data[2].name
              })
            }
            this.loading = false
            this.disabled = false
          }).catch((error) => {
            this.loading = false
            this.disabled = false
          })
          this.getSons(areaData[1], 2) // 
          this.getSons(areaData[2], 3) // /
        } else {
          this.$alert(res.msg, '', {type: 'error'}).then(() => {}).catch(() => {})
        }
        this.loading = false
        this.disabled = false
      }).catch((err) => {
        this.loading = false
        this.disabled = false
      })
    },
    getProvince () {
      // Id
      let select = this.provinceList.filter((item, index) => {
        if (this.projectForm.province == item.name)
          return item
      })[0] || {}
      this.provinceWatch = select.id || 0
    },
    getCity () {
      // Id
      let select = this.cityList.filter((item, index) => {
        if (this.projectForm.city == item.name)
          return item
      })[0] || {}
      this.cityWatch = select.id || 0
    },
    getCounty () {
      // Id
      let select = this.countyList.filter((item, index) => {
        if (this.projectForm.county == item.name)
          return item
      })[0] || {}
      this.countyWatch = select.id || 0
    }
  },
  watch: {
    provinceWatch: function (newData, oldData) {
      // Id
      this.getSons(newData, 2, true)
    },
    cityWatch: function (newData, oldData) {
      // Id
      this.getSons(newData, 3, true)
    },
    countyWatch: function (newData, oldData) {
      // Id
    }
  },
  created () {
    if (this.$route.params.id) {
      this.getProjectData(this.$route.params.id)
    } else {
      this.loading = false
      this.disabled = false
    }
  },
  mounted () {
    this.getSons(0, 1) // 
    !this.$route.params.id ?
      this.mapview(this.projectForm.longitude, this.projectForm.latitude)
    : this.mapview(116.403963, 39.915119) //    
  }
}
</script>

<style scoped>

</style>




