<template>
  <div class="customerInfo">
    <el-row>
      <el-col :span="24">
        <el-form
          v-loading="loading"
          :model="infoForm"
          :rules="rules"
          ref="infoForm"
          label-width="100px">
          <el-row type="flex" justify="space-around">
            <el-col :span="16">
              <el-row>
                <el-col :span="24">
                  <el-form-item label="">
                    <div>{{infoForm.name}}</div>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row>
                <el-col :span="24">
                  <el-form-item label="" prop="linkmanName">
                    <el-input
                      type="text"
                      placeholder=""
                      v-model.trim="infoForm.linkmanName" />
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row>
                <el-col :span="24">
                  <el-form-item label="" prop="linkmanPhone">
                    <el-input
                      type="text"
                      placeholder=""
                      v-model.trim="infoForm.linkmanPhone" />
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row>
                <el-col :span="24">
                  <el-form-item label="">
                    <div>{{infoForm.areaPathName}}</div>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row>
                <el-col :span="24">
                  <el-form-item label="">
                    <el-input
                      type="text"
                      maxLength="30"
                      placeholder="(30)"
                      v-model.trim="infoForm.address" />
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row>
                <el-col :span="24">
                  <el-form-item label="">
                    <el-input
                      type="textarea"
                      maxLength="150"
                      placeholder="(150)"
                      :rows="5"
                      v-model.trim="infoForm.introduction" />
                  </el-form-item>
                </el-col>
              </el-row>
            </el-col>
            <el-col :span="6" style="text-align: center">
              <div class="avatarBox">
                <el-upload
                  class="avatar-uploader"
                  :action="uploadLink + 'customer/upload'"
                  :show-file-list="false"
                  :on-success="handleAvatarSuccess"
                  :before-upload="beforeAvatarUpload"
                  :disabled="picDisabled">
                  <img v-if="infoForm.licensePic" :src="infoForm.licensePic" class="avatar" />
                  <div v-else>
                    <i class="el-icon-upload"></i>
                    <p class="">{{picDisabled ? '...' : ''}}</p>
                  </div>
                </el-upload>
                <el-button
                  v-if="infoForm.licensePic"
                  class="avatarButton"
                  size="small"
                  @click="dialogTableVisible = true"
                  type="primary"></el-button>
              </div>
            </el-col>
          </el-row>
          <el-row style="text-align:center">
            <el-col>
              <el-button
                type="primary"
                @click="submitForm('infoForm')"
                :disabled='disabled'></el-button>
            </el-col>
          </el-row>
        </el-form>
      </el-col>
    </el-row>
    <el-dialog
      style="text-align: center"
      width="70%"
      top="2%"
      :append-to-body="true"
      :visible.sync="dialogTableVisible"
      :modal-append-to-body="false">
      <img class="loginPic" :src="infoForm.licensePic" />
    </el-dialog>
  </div>
</template>

<script>
import {
  customerGetById,
  customerUpdate
} from '../../config/api'
import config from '../../config'
import {isLegalImg} from '../../config/common'
export default {
  name: 'customerInfo',
  data () {
    var checkMobile = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(''))
      }
      setTimeout(() => {
        if (!/[-\d]+/.test(value)) {
          callback(new Error(''))
        }
        callback()
      }, 1000)
    }
    return {
      disabled: false, // 
      picDisabled: false, // 
      loading: false, // loading
      uploadLink: config.baseUrl, // 
      dialogTableVisible: false, // dialog
      infoForm: {
        name: '', // 
        linkmanName: '', // 
        linkmanPhone: '', // 
        areaPathName: '', // 
        address: '', // 
        introduction: '', // 
        licensePic: '', // 
        id: this.$route.params.id || 0, // id
        applyTime: '',
        areaId: '',
        examineTime: '',
        licenseCode: ''
      },
      rules: {
        linkmanName: [{required: true, message: '', trigger: 'blur'}],
        linkmanPhone: [
          {required: true, message: '', trigger: 'blur'},
          {validate: checkMobile, trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    submitForm (formName) {
      // 
      this.$refs[formName].validate((valid) => {
        if (!this.infoForm.linkmanName
          || !/[-\d]+/.test(this.infoForm.linkmanPhone))
          return console.log('')
        // 
        this.disabled = true
        customerUpdate(this.infoForm).then((res) => {
          if (Number(res.code) != 1) {
            this.loading = false
            return this.$alert(res.msg, '', {type: 'error'}).then(() => {}).catch(() => {})
          }
          this.loading = false
          this.disabled = false
          this.$router.push({path: '/customer', query: {page: this.$route.query.page}})
        }).catch((err) => {
          this.loading = false
          this.disabled = false
        })
      })
    },
    getById () {
      // 
      customerGetById({
        id: this.$route.params.id || ''
      }).then((res) => {
        if (Number(res.code) != 1) {
          this.loading = false
          return this.$alert(res.msg, '', {type: 'error'}).then(() => {}).catch(() => {})
        }
        this.loading = false
        Object.assign(this.infoForm, res.data, {licensePic: res.msg + '/' +  res.data.licensePic})
      }).catch((err) => {})
    },
    handleAvatarSuccess (res, file) {
      // 
      this.picDisabled = false
      this.$message.success('')
      this.infoForm.licensePic = (res.msg + res.data) || ''
    },
    beforeAvatarUpload (file) {
      if (this.infoForm.licensePic !== '') {
        return this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          return isLegalImg(this, file, {
            success: () => {
              this.$message.warning('...')
              this.picDisabled = true
            },
            error: () => {
              this.picDisabled = false
            }
          })
        })
      } else {
        return isLegalImg(this, file, {
          success: () => {
            this.$message.warning('...')
            this.picDisabled = true
          },
          error: () => {
            this.picDisabled = false
          }
        })
      }
    }
  },
  created () {
    this.getById()
  }
}
</script>

<style scoped>
  .avatar {
    width: 100%;
  }
  .avatarBox {
    width: 240px;
    height: auto;
    position: relative;
    text-align: center;
  }
  .avatarButton {
    position: absolute;
    z-index: 999;
    bottom: 10px;
    right: 10px;
  }
  .el-icon-upload {
    font-size: 67px;
    color: #c0c4cc;
    margin: 40px 0 16px;
    line-height: 50px;
  }
  .el-upload__text {
    color: #606266;
    font-size: 14px;
    text-align: center;
  }
  .loginPic {
    max-width: 100%;
    height: 750px;
    border: 1px solid #666
  }

  @media only screen and (max-height : 768px) {
    .loginPic {
      max-width: 100%;
      height: 500px;
      border: 1px solid #666
    }
  }
</style>




