<template>
  <div class='dataAnalysis'>
    <el-row type="flex" justify="space-around">
      <el-col :span="12">
        <div id="drawPic" :style="{width: '600px', height: '400px'}"></div>
      </el-col>
      <el-col :span="12">
        <div id="drawBar" :style="{width: '600px', height: '400px'}"></div>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <div id="drawLine" :style="{width: '1600px', height: '400px'}"></div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  export default {
    name: 'dataAnalysis',
    data() {
      return {

      }
    },
    methods: {
      drawBar() {
        let drawBar = this.$echarts.init(document.getElementById('drawBar'))
        drawBar.setOption({
          title: {
            text: 'XXXXX',
            textStyle: {
              align: 'center'
            }
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: { // 
              type: 'shadow' // 'line' | 'shadow'
            }
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
          xAxis: [{
            type: 'category',
            data: ['', '', '', '', '', '', '']
          }],
          yAxis: [{
            type: 'value'
          }],
          series: [{
              name: '',
              stack: '',
              type: 'bar',
              data: [2, 3, 1, 5, 7, 4, 2]
            },
            {
              name: '',
              stack: '',
              type: 'bar',
              data: [120, 132, 101, 134, 290, 230, 220]
            },
            {
              name: '',
              stack: '',
              type: 'bar',
              data: [60, 72, 71, 74, 190, 130, 110]
            },
            {
              name: '',
              stack: '',
              type: 'bar',
              data: [62, 82, 91, 84, 109, 110, 120]
            }
          ]
        })
      },
      drawPic() {
        let drawPic = this.$echarts.init(document.getElementById('drawPic'))
        drawPic.setOption({
          title: {
            text: '',
          },
          tooltip: {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c} ({d}%)"
          },
          toolbox: {
            show: true,
            feature: {
              mark: {
                show: true
              },
              dataView: {
                show: true,
                readOnly: false
              },
              magicType: {
                show: true,
                type: ['pie', 'funnel']
              },
              restore: {
                show: true
              },
              saveAsImage: {
                show: true
              }
            }
          },
          calculable: true,
          series: [{
            name: '',
            type: 'pie',
            radius: [30, 110],
            roseType: 'area',
            data: [{
                value: 10,
                name: '1'
              },
              {
                value: 5,
                name: '2'
              },
              {
                value: 15,
                name: '3'
              },
              {
                value: 25,
                name: '4'
              },
              {
                value: 20,
                name: '5'
              },
              {
                value: 35,
                name: '6'
              },
              {
                value: 30,
                name: '7'
              },
              {
                value: 40,
                name: '8'
              }
            ]
          }]
        })
      },
      drawLine() {
        let drawLine = this.$echarts.init(document.getElementById('drawLine'))
        drawLine.setOption({
          title: {
            text: ''
          },
          legend: {
            data: ['', '', '', '', '']
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'cross',
              label: {
                backgroundColor: '#6a7985'
              }
            }
          },
          toolbox: {
            feature: {
              saveAsImage: {}
            }
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
          xAxis: [{
            type: 'category',
            boundaryGap: false,
            data: ['', '', '', '', '', '', '', '', '', '', '', '']
          }],
          yAxis: [{
            type: 'value'
          }],
          series: [{
              name: '',
              type: 'line',
              stack: '',
              areaStyle: {
                normal: {}
              },
              data: [120, 132, 101, 134, 90, 230, 210, 220, 182, 191, 234, 290, 330, 310]
            },
            {
              name: '',
              type: 'line',
              stack: '',
              areaStyle: {
                normal: {}
              },
              data: [220, 182, 191, 234, 290, 330, 310, 150, 232, 201, 154, 190, 330, 410]
            },
            {
              name: '',
              type: 'line',
              stack: '',
              areaStyle: {
                normal: {}
              },
              data: [150, 232, 201, 154, 190, 330, 410, 320, 332, 301, 334, 390, 330, 320]
            },
            {
              name: '',
              type: 'line',
              stack: '',
              areaStyle: {
                normal: {}
              },
              data: [320, 332, 301, 334, 390, 330, 320, 820, 932, 901, 934, 1290, 1330, 1320]
            },
            {
              name: '',
              type: 'line',
              stack: '',
              label: {
                normal: {
                  show: true,
                  position: 'top'
                }
              },
              areaStyle: {
                normal: {}
              },
              data: [820, 932, 901, 934, 1290, 1330, 1320, 150, 232, 201, 154, 190, 330, 410]
            }
          ]
        })
      }
    },
    mounted() {
      this.drawBar()
      this.drawPic()
      this.drawLine()
    }
  }

</script>



