<template>
  <div class='user'>
    <el-row>
      <el-col :span="4">
        <el-tree
          ref="tree"
          node-key="id"
          v-loading="treeLoading"
          :data="treeData"
          :default-expand-all="true"
          :highlight-current="true"
          :expand-on-click-node="false"
          :props="defaultProps"
          @node-click="handleNodeClick" />
      </el-col>
      <el-col :span="20" style="padding-left:20px">
        <el-form
          :inline="true"
          :model="formInline"
          ref="formInline"
          class="demo-form-inline">
          <el-form-item label="">
            <el-input
              v-model.trim="formInline.name"
              placeholder="" />
          </el-form-item>
          <el-form-item label="">
            <el-date-picker
              v-model="formInline.time"
              type="daterange"
              format="yyyy-MM-dd"
              value-format="yyyy-MM-dd"
              range-separator=""
              start-placeholder=""
              end-placeholder="" />
          </el-form-item>
          <el-form-item>
            <el-button
              type="primary"
              @click="getPageList()"
              :disabled="disabled"></el-button>
            <el-button
              type="primary"
              :disabled="disabled"
              @click="resetForm('formInline')"
              plain></el-button>
            <el-button
              @click="$router.push({path: '/user/addUser/0', query: {page: currentPage}})"
              type="primary"></el-button>
          </el-form-item>
        </el-form>
        <el-table
          :data="list"
          v-loading="loading"
          style="width: 100%">
          <el-table-column
            prop="phone"
            label="" />
          <el-table-column
            prop="realname"
            label="" />
          <el-table-column
            prop="phone"
            label="" />
          <el-table-column
            prop="duty"
            label="" />
          <el-table-column
            prop="ctime"
            label="" />
          <el-table-column
            prop="state"
            label="">
            <template slot-scope="scope">
              <el-tag :type="scope.row.state == 1 ? 'success' : 'danger'" close-transition>
                {{scope.row.state == 1 ? '' : ''}}
              </el-tag>
            </template>
          </el-table-column>
          <el-table-column
            label=""
            width="300"
            fixed="right">
            <template slot-scope="scope">
              <el-button
                size="small"
                type="text"
                @click="$router.push({
                  path: '/user/addUser/' + scope.row.id,
                  query: {page: currentPage}
                })"></el-button>
              <el-button
                size="small"
                @click="deleteAction(scope.row.id)"
                type="text"></el-button>
              <el-button
                size="small"
                @click="scope.row.state == 1 ? disableState(scope.row.id) : enableState(scope.row.id)"
                type="text">{{scope.row.state == 1 ? '' : ''}}</el-button>
              <el-button
                size="small"
                @click="resetPwd(scope.row.id)"
                type="text"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-row v-if="total > currentPageSize">
          <el-col :span="24">
            <el-pagination
              background
              style="text-align:center" class="mt20"
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page.sync="currentPage"
              :page-size="currentPageSize"
              layout="prev, pager, next, jumper"
              :total="total">
            </el-pagination>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
  </div>
</template>
<script>
  import {
    userGetPageList,
    userDisableState,
    userEnableState,
    userResetPwd,
    userDelete,
    userGetListBy
  } from '../../config/api'
  export default {
    data() {
      return {
        formInline: {
          name: '', // 
          time: [] // 
        },
        treeData: [{
          id: 1,
          realname: 'damin',
          pid: 0,
          children: []
        }], // 
        defaultProps: {
          children: 'children',
          label: 'realname'
        },
        list: [], // 
        treeLoading: true, // treeloading
        loading: true, // loading
        disabled: false, // 
        currentPage: Number(this.$route.query.page) || 1, // 
        currentPageSize: 10, // 
        total: 0, // 
        pid: 1, // Id
      }
    },
    methods: {
      resetForm (formName) {
        // 
        this.$refs[formName].resetFields()
        Object.assign(this.formInline, {
          name: '',
          time: []
        })
        this.getPageList()
      },
      getPageList () {
        // 
        userGetPageList({
          pageIndex: this.currentPage,
          pageSize: this.currentPageSize,
          pid: this.pid,
          registerEndTime: this.formInline.time[1] || '',
          registerStartTime: this.formInline.time[0] || '',
          userName: this.formInline.name || ''
        }).then(res => {
          if (Number(res.code != 1)) {
            this.$alert(res.msg, '', {type: 'error'}).then(() => {}).catch(() => {})
          } else {
            this.list = res.data.list
            this.total = res.data.total
            this.currentPage = res.data.pageNum
            this.currentPageSize = res.data.pageSize
          }
          this.disabled = false
          this.loading = false
        }).catch(err =>{
          this.disabled = false
          this.loading = false
        })
      },
      handleSizeChange (val) {
      this.currentPage = 1
      this.currentPageSize = val
      this.getPageList()
      },
      handleCurrentChange (val) {
        this.currentPage = val
        this.getPageList()
      },
      disableState (userId) {
        // 
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          userDisableState({
            id: userId
          }).then(res => {
            if (Number(res.code) != 1)
              return this.$alert(res.msg, '', {type: 'error'}).then(() => {}).catch(() => {})
            this.$message({
              message: '',
              type: 'success',
              duration: 2000
            })
            this.getPageList()
          }).catch(() => {})
        }).catch(err => {})
      },
      enableState (userId) {
        // 
        userEnableState({
          id: userId
        }).then(res => {
          if (Number(res.code) != 1)
            return this.$alert(res.msg, '', {type: 'error'}).then(() => {}).catch(() => {})
          this.$message({
            message: '',
            type: 'success',
            duration: 2000
          })
          this.getPageList()
        }).catch(err => {})
      },
      resetPwd (userId) {
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          userResetPwd({
            id: userId
          }).then(res => {
            if (Number(res.code) != 1)
              return this.$alert(res.msg, '', {type: 'error'}).then(() => {}).catch(() => {})
            this.getPageList()
            this.$message({
              message: '123456',
              type: 'success',
              duration: 2000
            })
          }).catch(() => {})
        }).catch(err => {})
      },
      deleteAction (userId) {
        // 
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          userDelete({
            id: userId
          }).then(res => {
            if (Number(res.code) != 1)
              return this.$alert(res.msg, '', {type: 'error'}).then(() => {}).catch(() => {})
            this.getPageList()
            this.$message({
              message: '',
              type: 'success',
              duration: 2000
            })
          }).catch(() => {})
        }).catch(err => {})
      },
      getListBy (id, pid, level) {
        // 
        userGetListBy({
          id: id
        }).then(res => {
          if (Number(res.code) != 1) {
            this.pid = pid || id // Id
            this.$alert(res.msg, '', {type: 'error'}).then(() => {}).catch(() => {})
          } else {
            this.addChild(id, this.treeData, res.data)
          }
          this.getPageList()
          this.treeLoading = false
        }).catch(err => {
          this.treeLoading = false
        })
      },
      addChild (id, list, data) {
        list.forEach(item => {
          // tree
          if (item.id === id) {
            return Object.assign(item, {children: data})
          } else {
            return item.children&&this.addChild(id, item.children)
          }
        })
      },
      handleNodeClick(data, nodeE) {
        // 
        console.log(nodeE)
        this.pid = data.id
        this.getListBy(data.id, nodeE.level)
      }
    },
    created () {
      this.getListBy(this.pid, 0, 1) // 
    }
  }

</script>



