<template>
  <div class="uncertified">
    <div v-if="flag == 0">
      <el-row>
        <el-col :span="24">
          <div class="box">
            <h2><i class="el-icon-info el_icon"></i> </h2>
            <p></p>
            <p>
              <span class="el_link" @click="$router.push({path: '/customer/infoInput/' + $route.params.id})"></span>
              <span class="el_link" @click="$router.push({path: '/customer/infoInput/' + $route.params.id})"></span>
              </p>
            <p>400-0123-123</p>
          </div>
        </el-col>
      </el-row>
    </div>
    <div v-if="flag == 2">
      <el-row>
        <el-col :span="24">
          <div class="box">
            <h2><i class="el-icon-error el_icon"></i> </h2>
            <p></p>
            <p>
              <span class="el_link" @click="$router.push({path: '/customer/infoInput/' + $route.params.id})"></span>
              <span class="el_link" @click="$router.push({path: '/customer/infoInput/' + $route.params.id})"></span>
              </p>
            <p>400-0123-123</p>
            <p>400-0123-123</p>
          </div>
        </el-col>
      </el-row>
    </div>
    <div v-if="flag == 3">
      <el-row>
        <el-col :span="24">
          <div class="box">
            <h2><i class="el-icon-warning el_icon"></i> </h2>
            <p></p>
            <p></p>
            <p>400-0123-123</p>
          </div>
        </el-col>
      </el-row>
      <el-row style="text-align: center">
        <el-col :span="24">
          <el-button type="primary" @click="$router.push({path: '/customer/infoInput/0'})"></el-button>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import {
  customerGetExamineStateByUser,
} from '../../config/api'
export default {
  name: 'uncertified',
  data () {
    return {
      flag: 3 //  3  0  1  2 
    }
  },
  methods: {
    getExamineStateByUser () {
      // 
      customerGetExamineStateByUser({
        id: this.$route.params.id || ''
      }).then((res) => {
        if (Number(res.code) != 1)
          return this.$alert(res.msg, '', {type: 'error'}).then(() => {}).catch(() => {})
        switch (res.data) {
          case 0, 2:
            this.flag = res.data
            break
          case 1:
            this.$router.push('/customer/customerInfo' + this.$route.params.id)
            break
          default:
            this.flag = res.data
        }
      })
    }
  },
  created () {
    if (this.$route.params.id == 0) {
      this.flag = 3
    } else {
      this.getExamineStateByUser()
    }
  }
}
</script>

<style scoped>
  .box {
    width: 300px;
    margin: 200px auto 20px;
  }
  h2 {
    text-align: center
  }
  p {
    padding-left: 57px
  }
  .el_icon {
    font-size: 28px;
  }
  .el_link {
    cursor: pointer;
    color: #409EFF;
  }
</style>





