<template>
  <div class="addUser">
    <h3></h3>
    <el-form
      v-loading="loading"
      :model="addForm"
      :rules="rules"
      ref="addForm"
      label-width="100px">
      <el-row>
        <el-col :span="12">
          <el-form-item label="" prop="realname">
            <el-input
              type="text"
              placeholder=""
              v-model.trim="addForm.realname" />
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="" prop="phone">
            <el-input
              type="text"
              placeholder=""
              v-model.trim="addForm.phone" />
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="" prop="password">
            <el-input
              type="password"
              placeholder="6-20"
              v-model.trim="addForm.password" />
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="" prop="email">
            <el-input
              type="text"
              placeholder=""
              v-model.trim="addForm.email" />
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="">
            <el-input
              type="text"
              placeholder=""
              v-model.trim="addForm.duty" />
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="" prop="state">
            <el-select v-model="addForm.state" placeholder="">
              <el-option
                v-for="item in stateList"
                :key="item.id"
                :label="item.value"
                :value="item.id"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <h3></h3>
      <el-form-item label="" prop="roleIdList">
        <el-row>
          <el-col :span="24">
            <el-checkbox-group v-model="addForm.roleIdList">
              <el-checkbox
                v-for="item in roleList"
                :label="item.id"
                :key="item.id">{{item.name}}</el-checkbox>
            </el-checkbox-group>
          </el-col>
        </el-row>
      </el-form-item>
      <el-row style="text-align:center">
        <el-col :span="24">
          <el-button
            type="primary"
            @click="submitForm('addForm')"
            :disabled='disabled'></el-button>
          <el-button @click="$router.push({path: '/user', query: {page: $route.query.page}})"></el-button>
        </el-col>
      </el-row>
    </el-form>
  </div>
</template>

<script>
import {
  userAdd,
  roleGetAllList,
  userGetById
} from '../../config/api'
export default {
  name: 'addUser',
  data () {
    // 
    var checkMobile = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(''))
      }
      setTimeout(() => {
        if (!/1\d{10}/.test(value)) {
          callback(new Error(''))
        }
        callback()
      }, 1000)
    }
    // 
    var checkCode =  (rule, value, callback) => {
      if (!value) {
        return callback(new Error(''))
      }
      setTimeout(() => {
        if (!/[a-zA-Z0-9]{6,20}/.test(value)) {
          callback(new Error('6-20'))
        }
        callback()
      }, 1000)
    }
    // 
    var checkEmail =  (rule, value, callback) => {
      if (!value) {
        return callback(new Error(''))
      }
      setTimeout(() => {
        if (!/\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*/.test(value)) {
          callback(new Error(''))
        }
        callback()
      }, 1000)
    }
    // 
    var checkSelect =  (rule, value, callback) => {
      console.log(value, 'value')
      if (!value) {
        return callback(new Error(''))
      }
      setTimeout(() => {
        if (!/\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*/.test(value)) {
          callback(new Error(''))
        }
        callback()
      }, 1000)
    }
    return {
      loading: false, // loading
      disabled: false, // disabled
      addForm: {
        duty: "", // 
        email: "", // 
        id: this.$route.params.id || 0, // id
        password: "", // 
        phone: "", // 
        realname: "", // 
        roleIdList: [], // 
        state: 1 // 
      },
      stateList: [
        {id: 0, value: ''},
        {id: 1, value: ''},
        {id: 2, value: ''},
      ], // 
      roleList: [], // 
      rules: {
        realname: [{required: true, message: '', trigger: 'blur'}],
        phone: [
          {required: true, message: '', trigger: 'blur'},
          {validator: checkMobile, trigger: 'blur'}
        ],
        password: [
          {required: true, message: '', trigger: 'blur'},
          {validator: checkCode, trigger: 'blur'}
        ],
        email: [{validator: checkEmail, trigger: 'blur'}],
        roleIdList: [{required: true, message: '', trigger: 'blur'}]
      }
    }
  },
  methods: {
    submitForm (formName) {
      // 
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.disabled = true
          userAdd(this.addForm).then(res => {
            if (Number(res.code) != 1) {
              this.$alert(res.msg, '', {type: 'error'}).then(() => {}).catch(() => {})
            } else {
              this.$message({
                type: 'success',
                message: '',
                duration: 2000
              })
              setTimeout(() => {
                this.$router.push({path: '/user', query: {page: this.$route.query.page}})
              }, 2000)
            }
            this.disabled = false
          }).catch(err => {
            this.disabled = false
          })
        } else return console.log('')
      })
    },
    getAllList () {
      // 
      roleGetAllList({}).then(res => {
        if (Number(res.code) != 1)
          return this.$alert(res.msg, '', {type: 'error'}).then(() => {}).catch(() => {})
        this.roleList = res.data
      }).catch(err => {})
    },
    getById () {
      // 
      userGetById({
        id: this.$route.params.id || 0
      }).then(res => {
        if (Number(res.code) != 1) {
          this.$alert(res.msg, '', {type: 'error'}).then(() => {}).catch(() => {})
        } else {
          Object.assign(this.addForm, res.data)
        }
        this.loading = false
        this.disabled = false
      }).catch(err => {
        this.loading = false
        this.disabled = false
      })
    }
  },
  created () {
    this.$route.params.id != 0 ? this.getById() : this.loading = false // 
    this.getAllList() // 
  }
}
</script>

<style scoped>
      
</style>





