<template>
  <div class='customer'>
    <el-row>
      <el-col :span="24">
        <el-form :inline="true" :model="formInline" ref="formInline" class="demo-form-inline">
          <el-form-item label="">
            <el-select v-model="formInline.customerId" placeholder="">
              <el-option
                v-for="item in customerList"
                :key="item.id"
                :label="item.name"
                :value="item.id" />
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="formInline.provinceId" :change="getProvince()" placeholder="">
              <el-option
                v-for="item in provinceList"
                :key="item.id"
                :label="item.name"
                :value="item.id" />
            </el-select>
            <el-select v-model="formInline.cityId" :change="getCity()" placeholder="">
              <el-option
                v-for="item in cityList"
                :key="item.id"
                :label="item.name"
                :value="item.id" />
            </el-select>
            <el-select v-model="formInline.countyId" :change="getCounty()" placeholder="">
              <el-option
                v-for="item in countyList"
                :key="item.id"
                :label="item.name"
                :value="item.id" />
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="formInline.state" placeholder="">
              <el-option
                v-for="item in stateList"
                :key="item.id"
                :label="item.name"
                :value="item.id" />
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button
              type="primary"
              @click="getCustomerPageList()"
              :disabled="disabled"></el-button>
            <el-button
              @click="ResetCustomerPageList()"
              :disabled="disabled" plain></el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <el-table
          v-loading="loading"
          :data="tableData"
          style="width: 100%"
          border>
          <el-table-column
            prop="name"
            label=""
            width="100"
            fixed />
          <el-table-column
            prop="areaPathName"
            label="" />
          <el-table-column
            prop="linkmanName"
            label=""
            width="100" />
          <el-table-column
            prop="linkmanPhone"
            label=""
            width="150" />
          <el-table-column
            prop="registerTime"
            label=""
            width="250" />
          <el-table-column
            prop="projectCount"
            label=""
            width="80" />
          <el-table-column
            prop="airStationCount"
            label=""
            width="80" />
          <el-table-column
            prop="userPhone"
            label=""
            width="150" />
          <el-table-column
            prop="state"
            label=""
            width="100">
            <template slot-scope="scope">
              <el-tag :type="scope.row.state == 1 ? 'success' : 'danger'" close-transition>
                {{scope.row.state == 1 ? '' : ''}}
              </el-tag>
            </template>
          </el-table-column>
          <el-table-column 
            label=""
            width="400"
            fixed="right">
            <template slot-scope="scope">
              <el-button
                size="small"
                @click="openDialog(scope.row.userId)"
                type="text"></el-button>
              <el-button
                size="small"
                type="text"
                @click="$router.push({
                  path: '/customer/customerInfo/' + scope.row.id,
                  query: {page: currentPage}
                })"></el-button>
              <el-button
                size="small"
                type="text"
                @click="$router.push({
                  path: '/customer/customerInfo/' + scope.row.id,
                  query: {page: currentPage}
                })"></el-button>
              <el-button
                size="small"
                @click="deleteAction(scope.row.id)"
                type="text"></el-button>
              <el-button
                size="small"
                @click="scope.row.state == 1 ? disableState(scope.row.id) : enableState(scope.row.id)"
                type="text">{{scope.row.state == 1 ? '' : ''}}</el-button>
              <el-button
                size="small"
                @click="resetPwd(scope.row.id)"
                type="text"></el-button> 
            </template>
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>
    <el-row v-if="total > currentPageSize">
      <el-col :span="24">
        <el-pagination
          background
          style="text-align:center" class="mt20"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="currentPageSize"
          layout="prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </el-col>
    </el-row>
    <el-dialog
      title=""
      :append-to-body="true"
      :visible.sync="dialogTableVisible"
      width="815px"
      :modal-append-to-body="false">
      <el-form :inline="true" :model="dialogForm" ref="dialogForm">
        <el-row>
          <el-col :span="10">
            <el-form-item label="">
              <el-input
                type="text"
                placeholder=""
                :value="dialogForm.projectName" />
            </el-form-item>
          </el-col>
          <el-col :span="10">
            <el-form-item label="">
              <el-input
                type="text"
                placeholder=""
                :value="dialogForm.ownerName" />
            </el-form-item>
          </el-col>
          <el-col :span="4">
            <el-form-item>
              <el-button
                type="primary"
                @click="submitFormDialog('dialogForm')"></el-button>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-checkbox-group v-model="checkList">
              <el-table
                v-loading="dialogLoading"
                :data="dialogList"
                style="width: 100%"
                border>
                <el-table-column
                  prop="value"
                  width="150"
                  label="">
                  <template slot-scope="scope">
                    <el-checkbox :label="scope.row.id" :checked="scope.row.isAllocated == 1 ? true : false" />
                  </template>
                </el-table-column>
                <el-table-column
                  prop="projectName"
                  label="" />
                <el-table-column
                  prop="ownerName"
                  label="" />
                <el-table-column
                  prop="customerName"
                  label="" />
              </el-table>
            </el-checkbox-group>
          </el-col>
        </el-row>
        <el-row style="text-align:center" class="mt30">
          <el-col :span="24">
            <el-form-item>
              <el-button
                type="primary"
                @click="submitDialog()"></el-button>
              <el-button @click="closeDialog()"></el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
  import {
    areaGetSons,
    customerGetAllList,
    customerGetCustomerPageList,
    projectGetUserAndNoUserPro,
    userDisableState,
    userEnableState,
    userResetPwd,
    userDelete
  } from '../../config/api'
  export default {
    data() {
      return {
        loading: true, // loading
        disabled: false, // 
        dialogTableVisible: false, // dialog
        dialogLoading: true, // loading
        formInline: {
          customerId: 0, // Id
          state: 0, // 
          provinceId: 0, // 
          cityId: 0, // 
          countyId: 0, // 
        },
        dialogForm: {
          ownerName: '', // 
          projectName: '', // 
          userId: '' // id
        },
        dialogList: [], //  
        checkList: [], // 
        customerList: [], // 
        stateList: [
          {id: 0, name: ''},
          {id: 1, name: ''},
          {id: 2, name: ''},
        ], // 
        provinceList: [{id: 0, name: ''}], // 
        cityList: [{id: 0, name: ''}], // 
        countyList: [{id: 0, name: ''}], // 
        provinceWatch: 0, // Id
        cityWatch: 0, // Id
        countyWatch: 0, // Id
        tableData: [], // 
        currentPage: Number(this.$route.query.page) || 1, // 
        currentPageSize: 10, // 
        total: 0, // ,
      }
    },
    methods: {
      getAllList () {
        // 
        customerGetAllList({})
          .then(res => {
            if (Number(res.code) != 1)
              return this.$alert(res.msg, '', {type: 'error'}).then(() => {}).catch(() => {})
            res.data.unshift({id: 0, name: ''})
            this.customerList = res.data
          }).catch(err => {})
      },
      getSons (id, type) {
        // 
        areaGetSons({id: Number(id)}).then((res) => {
          if (Number(res.code) != 1)
            return this.$alert(res.msg, '', {type: 'error'}).then(() => {}).catch(() => {})
          res.data.unshift({id: 0, name: ''})
          switch (type) {
            case 1:
              this.provinceList = res.data
              break
            case 2:
              this.cityList = res.data
              break
            case 3:
              this.countyList = res.data
              break
            default:
              this.provinceList = res.data
              break
          }
        }).catch((err) => {})
      },
      getProvince () {
        // Id
        this.provinceWatch = this.formInline.provinceId
      },
      getCity () {
        // Id
        this.cityWatch = this.formInline.cityId
      },
      getCounty () {
        // Id
        this.countyWatch = this.formInline.countyId
      },
      getCustomerPageList () {
        // 
        this.disabled = true
        customerGetCustomerPageList({
          customerId: this.formInline.customerId || 0,
          state: this.formInline.state || 0,
          provinceId: this.formInline.provinceId || 0,
          cityId: this.formInline.cityId || 0,
          countyId: this.formInline.countyId || 0,
          pageIndex: this.currentPage || 1,
          pageSize: this.currentPageSize || 10
        }).then(res => {
          if (Number(res.code != 1)) {
            this.$alert(res.msg, '', {type: 'error'}).then(() => {}).catch(() => {})
          } else {
            this.tableData = res.data.list
            this.total = res.data.total
            this.currentPage = res.data.pageNum
            this.currentPageSize = res.data.pageSize
          }
          this.loading = false
          this.disabled = false
        }).catch(err => {
          this.disabled = false
          this.loading = false
        })
      },
      ResetCustomerPageList () {
        // 
        Object.assign(this.formInline, {
          customerId: 0, // Id
          state: 0, // 
          provinceId: 0, // 
          cityId: 0, // 
          countyId: 0, // 
        })
        this.getCustomerPageList()
      },
      openDialog (userId) {
        // 
        this.dialogForm.userId = userId
        this.submitFormDialog(() => {this.dialogTableVisible = true})
      },
      submitFormDialog (cb) {
        // 
        projectGetUserAndNoUserPro(this.dialogForm)
          .then(res => {
            if (Number(res.code != 1)) {
              this.$alert(res.msg, '', {type: 'error'}).then(() => {}).catch(() => {})
            } else {
              this.dialogList = res.data
            }
            this.dialogLoading = false
            cb()
          })
          .catch(err => {})
      },
      submitDialog () {
        // 
        this.dialogTableVisible = false
      },
      closeDialog () {
        // 
        this.dialogTableVisible = false
        this.checkList = []
      },
      disableState (userId) {
        // 
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          userDisableState({
            id: userId
          }).then(res => {
            if (Number(res.code) != 1)
              return this.$alert(res.msg, '', {type: 'error'}).then(() => {}).catch(() => {})
            this.getAllList()
            this.$message({
              message: '',
              type: 'success',
              duration: 2000
            })
          }).catch(() => {})
        }).catch(err => {})
      },
      enableState (userId) {
        // 
        userEnableState({
          id: userId
        }).then(res => {
          if (Number(res.code) != 1)
            return this.$alert(res.msg, '', {type: 'error'}).then(() => {}).catch(() => {})
          this.getAllList()
          this.$message({
            message: '',
            type: 'success',
            duration: 2000
          })
        }).catch(err => {})
      },
      resetPwd (userId) {
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          userResetPwd({
            id: userId
          }).then(res => {
            if (Number(res.code) != 1)
              return this.$alert(res.msg, '', {type: 'error'}).then(() => {}).catch(() => {})
            this.getAllList()
            this.$message({
              message: '123456',
              type: 'success',
              duration: 2000
            })
          }).catch(() => {})
        }).catch(err => {})
      },
      deleteAction (userId) {
        // 
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          userDelete({
            id: userId
          }).then(res => {
            if (Number(res.code) != 1)
              return this.$alert(res.msg, '', {type: 'error'}).then(() => {}).catch(() => {})
            this.getAllList()
            this.$message({
              message: '',
              type: 'success',
              duration: 2000
            })
          }).catch(() => {})
        }).catch(err => {})
      },
      handleSizeChange (val) {
        this.currentPage = 1
        this.currentPageSize = val
        this.getCustomerPageList()
      },
      handleCurrentChange (val) {
        this.currentPage = val
        this.getCustomerPageList()
      },
    },
    watch: {
      provinceWatch: function (newData, oldData) {
        // Id
        this.getSons(newData, 2)
      },
      cityWatch: function (newData, oldData) {
        // Id
        this.getSons(newData, 3)
      },
      countyWatch: function (newData, oldData) {
        // Id
      }
    },
    created() {
      this.getSons(0, 1) // 
      this.getAllList() // 
      this.getCustomerPageList() // 
    },
  }

</script>



