import Vue from 'vue';
import axios from 'axios';
import router from '../router';
import qs from 'qs'
import config from '../config'
//axios
axios.defaults.timeout = 10000;
//
axios.defaults.baseURL = config.baseUrl;
// axios.defaults.withCredentials = true;
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=utf-8'; //post
// http request 
axios.interceptors.request.use(config => {
    // 
    config.headers.UUID = 123;
    return config;
}, error => {
    return Promise.reject(error);
});

// http response 
axios.interceptors.response.use(response => {
    // if (response.data.result === 2000) {
    //     // token token
    //     store.commit(types.LOGOUT);
    //     router.push({ path: '/login' });
    // }
    return response.data;
}, err => {
    console.log(err)
        // 200
    if (err && err.response) {
        switch (err.response.status) {
            case 400:
                err.message = '';
                break;

            case 401:
                err.message = '';
                break;

            case 403:
                err.message = '';
                break;

            case 404:
                err.message = `: ${err.response.config.url}`;
                break;

            case 408:
                err.message = '';
                break;

            case 500:
                err.message = '';
                break;

            case 501:
                err.message = '';
                break;

            case 502:
                err.message = '';
                break;

            case 503:
                err.message = '';
                break;

            case 504:
                err.message = '';
                break;

            case 505:
                err.message = 'HTTP';
                break;

            default:
                err.message = err.response.message
                break;
        }
    }
    Vue.prototype.$alert(err.toString(), '', { type: 'error' }).then(() => {}).catch(() => {});
    return Promise.reject(err);
});



//Vue.prototype.$http = axios; //axiosVue
// http://blog.csdn.net/sjn0503/article/details/74729300
// axios.install = (Vue) => {
//   Vue.prototype.$axios = axios;
// }

export default axios;


